import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-weatherhcm',
  templateUrl: './weatherhcm.component.html',
  styleUrls: ['./weatherhcm.component.css']
})
export class WeatherhcmComponent implements OnInit {
  CityName: String ="Thanh pho ho chi minh";
  response: any;
  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.http.get('https://api.openweathermap.org/data/2.5/weather?&appid=c0b0afd751713aaf7a94ea14df6d555c&units=metric&q='+ this.CityName)
    .subscribe((response) => {
      this.response = response;
      console.log(this.response);
    })
  }

}