import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-weatherhanoi',
  templateUrl: './weatherhanoi.component.html',
  styleUrls: ['./weatherhanoi.component.css']
})
export class WeatherhanoiComponent implements OnInit {
  CityName: String ="Hà Nội";
  response: any;
  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.http.get('https://api.openweathermap.org/data/2.5/weather?&appid=c0b0afd751713aaf7a94ea14df6d555c&units=metric&q='+ this.CityName)
    .subscribe((response) => {
      this.response = response;
      console.log(this.response);
    })
  }

}